            }
        }
    }
    .effect-cls {
        &:before,
        &:after {
            filter: invert(100);
        }
    }
    .game-product {
        .product-box {
            .cart-info {
                a {
                    i {
                        background-color: $dark-body;
                        transition: all 0.3s ease;
                        color: $dark-link;
                    }
                }
            }
        }
    }
    .demo-right {
        a {
            background-color: $black;
            transition: all 0.3s ease;
        }
    }
    .rtl-btn,
    .dark-light {
        background-color: $black;
        box-shadow: none;
    }
    .color-picker {
        a {
            &.handle {
                background-color: $black;
                transition: all 0.3s ease;
                color: $dark-link;
            }
        }
    }
    .setting-box {
        .setting-title {
            h4 {
                color: $font-color;
            }
        }
    }
    .add_to_cart {
        .cart-inner {
            background-color: $dark-body;
            .cart_media {
                li {
                    .media {
                        .media-body {
                            h4 {
                                color: rgba(white, 0.7);
                            }
                        }
                    }
                    .total {
                        border-color: $dark-border;
                    }
                }
            }
            .cart_top {
                border-color: $dark-border;
                .close-cart {
                    i {
                        color: $white;
                    }
                }
                h3 {
                    color: $white;
                }
            }
        }
        &.bottom,
        &.top {
            .cart-inner {
                .cart_top {
                    background-color: $dark-top;
                }
            }
        }
    }
    .stripe-section {
        background-color: $dark-top;
        border-color: $dark-border;
        .content {
            h5 {
                color: rgba(white, 0.8);
            }
            table {
                tr {
                    border-color: $dark-border;
                }
            }
        }
    }
}