                            right: 15px;
                            width: 34px;
                            height: 34px;
                            overflow: hidden;
                            font: bold 16px/34px monospace !important;
                            text-align: center;
                            text-shadow: none;
                            border-radius: 4px;
                            &:before {
                                content: '+';
                                font-family: inherit;
                                position: relative;
                            }
                        }
                    }
                    ul {
                        position: relative;
                        left: 0;
                        width: 100%;
                        display: none;
                        padding: 5px 0;
                        &.opensub1 {
                            display: block;
                        }
                        li {
                            a {
                                font-size: 12px;
                                font-weight: 400;
                            }
                            ul {
                                display: none;
                                background: rgba(255, 85, 69, 0.04);
                                &.opensub2 {
                                    display: block;
                                }
                                li {
                                    a {
                                        padding: 5px 45px;
                                    }
                                    ul {
                                        display: none;
                                        background: rgba(255, 85, 69, 0.04);
                                        &.opensub3 {
                                            display: block;
                                        }
                                        li {
                                            ul {
                                                display: none;
                                                &.opensub4 {
                                                    display: block;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .mega-menu {
                        width: 100% !important;
                        padding: 0 30px !important;
                        position: relative;
                        display: none;
                        left: 0;
                        &.opensidesubmenu {
                            display: block;
                        }
                        .link-section {
                            h5 {
                                font-weight: 700;
                                font-size: 14px;
                                text-transform: capitalize;
                            }
                            ul {
                                display: block;
                                padding: 0;
                                background: #fff;
                                li {
                                    a {
                                        padding: 5px 0px 5px 10px !important;
                                        font-size: 12px;
                                        font-weight: 400;
                                    }
                                }
                            }
                        }
                        .mega-menu-banner {
                            padding: 5px 0;
                            img {
                                padding: 20px 0 0;
                            }
                        }
                    }
                }
            }
        }
    }
    .header-2 {
        .main-navbar {
            .nav-menu {
                >li {
                    >a {
                        padding-top: 10px;
                        padding-bottom: 10px;
                    }
                }
            }
        }
    }
    .mega-box {
        .link-section {
            .demo {
                ul {
                    li {
                        a {
                            span {
                                img {
                                    top: 30px;
                                    right: -60px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .mega-box {
        &.col {
            flex-basis: unset;
        }
    }
    .sidenav {
        overflow-y: auto;
    }
    .main-menu {
        &.border-section {
            border: none;
        }
        .menu-left {
            .main-menu-right {
                .toggle-nav {
                    position: absolute;
                    z-index: 2;
                    right: 145px;
                    top: 6px;
                    .sidebar-name {
                        font-size: 18px;
                        padding-left: 5px;
                        text-transform: uppercase;
                    }
                }
            }
        }
    }
    .main-nav-center {
        .toggle-nav {
            position: absolute;
            z-index: 99;
            right: 175px;
            top: -105px;
        }
    }
    .layout3-menu {
        .main-menu {
            .menu-left {
                .main-menu-right {
                    .toggle-nav {
                        right: unset;
                        top: -1px;
                    }
                }
            }
        }
    }
    .rtl {
        .mega-box {
            .link-section {
                .demo {
                    ul {
                        li {
                            a {
                                span {
                                    img {
                                        left: -70px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        .pixelstrap {
            a {
                .sub-arrow {
                    right: unset;
                    left: 0;
                }
            }
            li {
                .lable-nav {
                    left: unset;
                    right: 15px;
                    &:before {
                        left: unset;
                        right: 5px;
                    }
                }
            }
        }
    }
}

@media (max-width: 991px) {
    .top-header {
        .header-contact {
            display: none;
        }
    }
}

@media (min-width: 578px) {
    .main-menu {
        .menu-right {
            .icon-nav {
                .mobile-search,
                .mobile-cart,
                .mobile-setting {
                    i {
                        display: none;
                    }
                    .close-circle {
                        i {
                            display: block;
                        }
                    }
                }
                .mobile-search {
                    .show-div {
                        .form-inline {
                            button {
                                i {
                                    display: inline-block;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

