                        color: $header-font;
                    }
                    >div {
                        img {
                            display: none;
                        }
                    }
                }
                .mobile-search {
                    right: 80%;
                    z-index: 1;
                    i {
                        display: inline-block;
                        font-size: 22px;
                        color: $header-font;
                    }
                    img {
                        display: none;
                    }
                    .show-div {
