}

.owl-prev,
.owl-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}

.sticky.fixed {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 999;
  -webkit-transition: all 300ms ease;
  -moz-transition: all 300ms ease;
  -ms-transition: all 300ms ease;
  -o-transition: all 300ms ease;
  transition: all 300ms ease;
  background: #fff;
  box-shadow: 0px 0px 8px #ddd;
}
.sticky.fixed .top-header {
  display: none;
}
.sticky.header2.fixed .top-header {
  display: none;
}
.sticky.header2.fixed .main-menu .brand-logo {
  padding-top: 26px;
  padding-bottom: 26px;
}
.sticky.header2.fixed .main-menu .menu-left .navbar {
  padding: 28px 45px 28px 0;
}
.sticky.header2.fixed .main-menu .menu-right .icon-nav li.onhover-div {
  padding-top: 30px;
  padding-bottom: 30px;
}
.sticky.header2.fixed .main-nav-center .sm > li > a {
  padding-top: 16px;
  padding-bottom: 16px;
}
.sticky.header2.fixed .main-nav-center .sm > li .lable-nav {
  top: 1px;
}

.rtl-btn {
  position: fixed;
  right: 0;
  width: 42px;
  height: 41px;
  color: #000000;
  transition: all ease-in 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
  text-decoration: none;
  background: #ffffff;
  top: 33%;
  border: 1px solid white;
  -webkit-box-shadow: 0 0 5px rgba(4, 4, 4, 0.28);
  box-shadow: 0 0 5px rgba(4, 4, 4, 0.28);
}
.rtl-btn .RTL_cls {
  position: relative;
  z-index: 99;
  display: block;
  float: right;
  font-size: 14px;
  text-decoration: none;
  padding-left: 8px;
  white-space: nowrap;
  color: black;
}
.rtl-btn .RTL_cls .cart_qty_cls {
  font-weight: 400;
}

.bg-grey {
  background-color: #f7f7f7;
}

/*=====================
    1.1.Button CSS start
    ==========================*/
button {
  cursor: pointer;
}

.btn {
  line-height: 20px;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  border-radius: 0;
  transition: 0.3s ease-in-out;
}
.btn:hover {
  transition: 0.3s ease-in-out;
}
.btn:focus {
  box-shadow: none;
}

.btn-solid {
